import { Component, OnInit } from '@angular/core';
import {UserModel} from '../models/User.model';
import {UserService} from '../user/user.service';
import {BankService} from '../services/bank.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  _currentUser: UserModel;
  amount: number;
  constructor(private _userService: UserService, private  _bankService: BankService) { }

  ngOnInit() {
  }

  getParent(): void {
    this._currentUser = this._userService.getParent();
    console.log(this._currentUser);
  }

  getChildren(): void {
    this._currentUser = this._userService.getChildren();
    console.log(this._currentUser);
  }
  add(amount): void {
    this._bankService.addAmount(amount);
  }
}



