import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Http} from '@angular/http';
import {BankModel} from '../models/bank.model';
import {TypeTransaction} from '../models/type.transaction.enum';
import {Transaction} from '../models/transaction.model';

@Injectable()
export class BankService {

  constructor(private http: Http) {
  }

    getTotal(): Observable<BankModel> {
       return this.http.get('/api/piggy-bank/get-account')
       .map(res => res.json());
    }
    addAmount(amount): void {
      console.log(amount);
      this.http.post('piggy-bank/add-money/', {amount: amount});
    }
    /*getTotal(): BankModel {
      return {
        amount: 300,
        history: [
          {amount: 30, date: new Date('2017-01-01'), type: TypeTransaction.retrait},
          {amount: 10, date: new Date('2017-05-12'), type: TypeTransaction.depot},
          {amount: 15, date: new Date('2017-06-02'), type: TypeTransaction.retrait}
        ]
      };
    }*/
}



